:root {
  --regular: 400;
  --medium: 500;
  --bold: 700;
  --italic: italic;
  --black: #111;
  --black-cat: #1119;
  --white-cat: #fff9;
  --popup-background: #1116;
  --grey-400: #b4afaf;
  --white: #fff;
  --purple: #4f2ee8;
  --yellow: #eac645;
  --filter-light: 0%;
  --filter-dark: invert(100%);
  --hero-background-color: #f6f6f6;
  --hero-background-color-dark: #202024;
}

p, h1, h2, h3, h4, h5, h6, ul, li {
