.authorization__form {
  display: flex;
  flex-direction: column;
}

.authorization__modal {
  width: 300px;
  padding: 40px 15px 15px;
  @media screen and (min-width: 375px) {
    width: 335px;
    padding: 40px 20px;
  }
  @media screen and (min-width: 768px) {
    width: 450px;
    padding: 65px 25px 25px;
  }
  @media screen and (min-width: 1440px) {
    width: 579px;
    padding: 80px 40px 40px;
  }
  position: absolute;

  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  background-color: var(--white);
  border: 2px solid var(--black);
  border-radius: 18px;
}
[data-theme='dark'] .authorization__modal {
  background: var(--black);
  border-color: var(--white);
}
.authorization__icon__close {
  display: block;

  width: 20px;
  height: 20px;

  @media screen and (min-width: 768px) {
    width: 28px;
    height: 28px;
  }

  transition-property: stroke;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  stroke: var(--black);

  &:hover,
  &:focus {
    stroke: var(--purple);
  }
}
[data-theme='dark'] .authorization__icon__close {
  stroke: var(--white);
  &:hover,
  &:focus {
    stroke: var(--purple);
  }
}
.authorization__button__close {
  position: absolute;
  cursor: pointer;
  top: 13px;
  right: 10px;

  @media screen and (min-width: 375px) {
    top: 14px;
    right: 2%;
  }
  @media screen and (min-width: 768px) {
    top: 20px;
    right: 20px;
  }
  @media screen and (min-width: 1440px) {
    top: 24px;
    right: 24px;
  }
  background-color: transparent;
  border: 0;
}

.authorization__field {
  display: block;
  position: relative;
  margin-bottom: 13px;

  @media screen and (min-width: 375px) {
    margin-bottom: 24px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 26px;
  }
  @media screen and (min-width: 1440px) {
    margin-bottom: 28px;
  }
}

.authorization__field:nth-child(3) {
  margin-bottom: 17px;

  @media screen and (min-width: 375px) {
    margin-bottom: 24px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
}

.authorization__input {
  padding: 10px 20px;
  font-size: 14px;
  transition: stroke 300ms;

  @media screen and (min-width: 375px) {
    height: 50px;

    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 24px;
    padding-right: 45px;
  }
  @media screen and (min-width: 768px) {
    height: 64px;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 40px;
    padding-right: 60px;

    font-size: 24px;
  }

  width: 100%;

  color: var(--black);
  background-color: inherit;
  border: 2px solid var(--black);
  border-radius: 40px;
  display: block;
}
[data-theme='dark'] .authorization__input {
  color: var(--white);
  border: 2px solid var(--white);
}
.authorization__label {
  position: absolute;

  top: 50%;
  transform: translateY(-50%);
  left: 20px;

  font-size: 13px;

  @media screen and (min-width: 375px) {
    left: 30px;

    font-size: 16px;
  }
  @media screen and (min-width: 768px) {
    left: 40px;
    font-size: 18px;
  }

  font-weight: 700;

  line-height: calc(24 / 18);

  letter-spacing: -0.01em;
  text-transform: uppercase;

  color: var(--black);

  cursor: pointer;

  transition: 300ms;
}
[data-theme='dark'] .authorization__label {
  color: var(--white);
}
.authorization__input:not(:placeholder-shown) + .authorization__label,
.authorization__input:focus + .authorization__label {
  top: -16%;
  font-size: 10px;
  @media screen and (min-width: 375px) {
    top: -23%;
    font-size: 12px;
  }

  @media screen and (min-width: 768px) {
    top: -20%;
    font-size: 15px;
  }
}

.authorization__input:focus,
.authorization__input:hover {
  outline: 2px solid var(--purple);
  transition: stroke 300ms;
}

.authorization__input:focus ~ .authorization__icon,
.authorization__input:hover ~ .authorization__icon {
  stroke: var(--purple);
  transition: stroke 300ms;
}

.authorization__input:valid {
  outline: 2px solid #70b509;
}

.authorization__input:valid ~ .authorization__icon {
  stroke: #70b509;
  transition: stroke 300ms;
}

.authorization__input:not(:placeholder-shown):not(:valid) {
  outline: 2px solid #db5353;
}

.authorization__input:not(:placeholder-shown):not(:valid)
  ~ .authorization__icon {
  stroke: #db5353;
  transition: stroke 300ms;
}

.authorization__icon {
  stroke: var(--black);
  position: absolute;

  top: 50%;
  transform: translateY(-50%);
  right: 20px;

  width: 15px;
  height: 15px;

  @media screen and (min-width: 375px) {
    width: 18px;
    height: 18px;

    right: 24;
  }

  @media screen and (min-width: 768px) {
    width: 28px;
    height: 28px;
  }

  @media screen and (min-width: 1440px) {
    right: 32;
  }
}
[data-theme='dark'] .authorization__icon {
  stroke: var(--white);
}

.authorization__btn__submit {
  cursor: pointer;
  padding: 10px 100px;
  color: var(--white);
  background: var(--black);
  border-radius: 40px;
  border: none;

  margin-bottom: 6px;

  @media screen and (min-width: 375px) {
    padding: 16px 113px;

    font-size: 16px;
    line-height: calc(18 / 16);
  }

  @media screen and (min-width: 768px) {
    padding: 20px 154px;
    margin-bottom: 7px;

    font-size: 15px;
    line-height: calc(24 / 15);
  }
  @media screen and (min-width: 1440px) {
    padding: 20px 213px;
    margin-bottom: 10px;

    font-size: 17px;
    line-height: calc(24 / 15);
  }

  font-weight: 700;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  transition-property: color, background-color;
  transition-duration: 0.25s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: var(--purple);
    color: var(--white);
  }
}

[data-theme='dark'] .authorization__btn__submit {
  background: var(--white);
  color: var(--black);
  &:hover,
  &:focus {
    background-color: var(--purple);
    color: var(--white);
  }
}

.authorization__link__block {
  text-align: center;
}

.authorization__link {
  cursor: pointer;

  font-weight: 700;
  font-size: 13px;
  @media screen and (min-width: 375px) {
    font-size: 14px;
    line-height: calc(18 / 14);
  }
  @media screen and (min-width: 768px) {
    font-size: 18px;
  }
  line-height: calc(24 / 18);

  letter-spacing: -0.01em;

  text-transform: uppercase;

  background-color: transparent;
  border: none;
  padding: 0;

  &:hover,
  &:focus {
    color: var(--purple);
  }
}

.active-link {
  color: var(--purple);
  text-decoration-line: underline;
}

.desactive-link {
  color: var(--grey-400);
}

.authorization__link:nth-child(1) {
  margin-right: 7px;

  @media screen and (min-width: 375px) {
    margin-right: 10px;
  }
  @media screen and (min-width: 768px) {
    margin-right: 15px;
  }
  @media screen and (min-width: 1440px) {
    margin-right: 20px;
  }
}

.display-form {
  display: none;
}

.authorization__bacdrop {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(32, 32, 36);
  background: linear-gradient(
    172deg,
    var(--hero-background-color-dark) 3%,
    var(--purple) 86%,
    var(--yellow) 97%
  );
}
